#wrapper {
  height: 100%;
  width: 90%;
  margin: 0px auto;
  overflow: hidden
}

/*-----------------
	1. General
-----------------------*/
:root {
  --color-primary: #1ebfff;
  --color-secondary: #09a7e5;
}

html,
body,
#root {
  margin: 0;
  height: 100%;
  min-height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
}

header,
footer {
  flex: none;
}

main {
  flex: auto;
}

@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,900&display=swap");

@font-face {
  font-family: "Material Icons";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/MaterialIcons-Regular.eot);
  /* For IE6-8 */
  src: local("Material Icons"), local("MaterialIcons-Regular"),
    url(../fonts/MaterialIcons-Regular.woff2) format("woff2"),
    url(../fonts/MaterialIcons-Regular.woff) format("woff"),
    url(../fonts/MaterialIcons-Regular.ttf) format("truetype");
  font-display: swap;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga";
}

html {
  height: 100%;
}

body {
  background-color: #f8f9fa;
  color: #272b41;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  height: 100%;
  overflow-x: hidden;
  /* overflow:hidden; */
  margin: 0
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #15558d;
  font-weight: 500;
}

.h1,
h1 {
  font-size: 2.25rem;
  padding-bottom: 3rem;
}

.h2,
h2 {
  font-size: 1.875rem;
  margin-bottom: 2rem;
}

.h3,
h3 {
  font-size: 1.5rem;
}

.h4,
h4 {
  font-size: 1.125rem;
}

.h5,
h5 {
  font-size: 1rem;
}

.h6,
h6 {
  font-size: 0.875rem;
}

a {
  color: #2e3842;
  text-decoration: none;
}

a:hover {
  color: var(--color-secondary);
}

.side-link a {
  color: #ffffff;
  text-decoration: none;
}

.side-link a:hover {
  color: var(--color-secondary);
}

.intext-link {
  color: var(--color-secondary);
}

.intext-link:hover {
  color: var(--color-primary);
}

a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

input,
button,
a {
  transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
}

button:focus {
  outline: 0;
}

input[type="text"],
input[type="password"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.btn.focus,
.btn:focus {
  box-shadow: inherit;
}

.form-control {
  border-color: #dcdcdc;
  color: #333;
  font-size: 15px;
  min-height: 46px;
  padding: 6px 15px;
}

.table .form-control {
  font-size: 14px;
  min-height: 38px;
}

.container-fluid {
  padding-inline-start: 30px;
  padding-inline-end: 30px;
}

.form-control::-webkit-input-placeholder {
  color: #858585;
  font-size: 14px;
}

.form-control::-moz-placeholder {
  color: #858585;
  font-size: 14px;
}

.form-control:-ms-input-placeholder {
  color: #858585;
  font-size: 14px;
}

.form-control::-ms-input-placeholder {
  color: #858585;
  font-size: 14px;
}

.form-control::placeholder {
