  font-size: 14px;
}

.list-group-item {
  border: 1px solid #f0f0f0;
}

.content {
  min-height: 200px;
  padding: 30px 0 0;
}

.styled-ul {
  list-style: none;
}

.styled-ul .styled-li:before {
  content: '';
  margin-inline: 8px;
