/* Header.module.css */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 5;
  background: transparent;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 1000;
  display: flex;
  align-items: center;
  padding: 0 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.transparent {
  background-color: transparent;
  color: #ffffff;
}

.solid {
  background-color: #ffffff;
  color: #333333;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.defaultLink {
  color: #ffffff; /* White for transparent background */
  transition: color 0.3s ease;
}

.scrolledLink {
  color: #000000; /* Dark for solid background */
  transition: color 0.3s ease;
}

/* Dropdown item styling to inherit colors */
.defaultLink .dropdown-item,
.scrolledLink .dropdown-item {
  color: inherit;
}

/* Header.module.css */

.logo-header {
  transition: opacity 0.3s ease;
}

.language-switcher {
  color: #f00;
}
