/* MenuPages.module.css */

.defaultLink {
  color: white !important;
  /* White for transparent background */
}

.scrolledLink {
  color: #555 !important;
  /* Dark for solid background */
}


/* Styling for the language toggle link */
.languageToggle {
  color: #fff !important;
  font-family: system-ui;
  font-weight: bold;
}

.languageToggle.scrolled {
  color: #555 !important;
}

/* Additional padding for the language toggle in mobile view */
@media (max-width: 768px) {
  .languageToggle {
    padding-top: 3rem !important;
  }
}